#chat-button {
    background: none;
    position: fixed;
	border: none;
    padding: 0;
    bottom: 3.5%;
    right: 2.5%;
    z-index: 10;
}

#chat-button:focus {
    box-shadow: none;
}

#chat-button-icon {
    width: 75px;
    transition: 0.5s;
}

#chat-button-icon:hover {
    opacity: 0.9;
}

#chat-button-icon:active {
    opacity: 0.75;
}

#background-video {
    position: fixed;
    width: 100%;
    height: 100vh;
    z-index: -1;
    object-fit: cover;
}

.custom-hr {
    height: 10px;
    border: none;
    background-color: #393939;
    margin: 0
}

.about-us {
    background-color: #EFF4FB;
}

#about-text-1 {
